.feed {
    flex: .5;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 15px;
    margin-right: 15px;
    padding: 10px;

}

.feed__inputContainer {
    display: flex;
    padding: 10px;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-radius: 10px;

}


.feed__input {
    border: 1px solid lightgray;
    border-radius: 30px;
    padding: 10px;
    display: flex;
    padding-left: 15px;
    color: gray;
}

.feed__input > input {
    display: flex;
    width: 100%;
}

.feed__input > form > input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
    width: 400px;
}

.feed__input > form > button {
    display: none;
}

.feed__inputoptions {
    display: flex;
    justify-content: space-evenly;
    color: gray;
    margin-top: 10px;

}