.sidebar {
    position: sticky;
    top: 80px;
    flex: 0.25;
    border-radius: 10px;
    text-align: center;
    height: fit-content;


}


.sidebar__avatar {
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.sidebar__top > h4 {
    color: gray;
    font-size: 12px;

}

.sidebar__top > h2 {
    color: black;
    font-size: 17px;
    font-weight: 700;

}

.sidebar__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}

.sidebar__top > img {
    margin-bottom: -25px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
}

.sidebar__stat {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.sidebar__stat > p {
    color: gray;
    margin-right: 10px;
    margin-left: 10px;
    font-size: 12px;
    font-weight: 700;
}

.sidebar__statNumber {
    font-weight: bold;
    color: rgb(0, 90, 226) !important;
}

.sidebar__stats {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

}

.sidebar__bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;

    margin-top: 10px;
}

.sidebar__bottom > p {
    color: rgb(0, 44, 189);
    font-size: 12px;
    font-weight: 700;
    margin-bottom: 10px;
    margin-top: 10px;
}

.sidebar__recentItem {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: gray;
    margin-top: 5px;
    margin-bottom: 5px;
    cursor: pointer;
    padding: 2px;
}

.sidebar__recentItem:hover {
    background-color: whitesmoke;
    border-radius: 10px;
    cursor: pointer;
    color: black;
}

.sidebar__hash {
    color: gray;
    font-size: 12px;
    font-weight: 700;
    margin-left: 5px;
    margin-right: 5px;
}

.sidebar__myitem {
    display: flex;
    border-top: 1px solid lightgray;
    padding-top: 10px;
    padding-bottom: 10px;
    color: gray;
    font-size: 12px;
    font-weight: 700;
}

.sidebar__myitem:hover {
    background-color: whitesmoke;
    border-radius: 10px;
    cursor: pointer;
    color: grey;

}

.sidebar__myitem > p {
    margin-left: 10px;
    padding-top: 5px;

}

.sidebar__myitem > .MuiSvgIcon-root {
    color: gray;
    font-size: 16px;
    padding-top: 5px;
}
