.widgets {
    position: sticky;
    top: 80px;
    flex: 0.25;
    background-color: white;
    border-radius: 10px;
    height: fit-content;
    padding-bottom: 10px;
    border: 1px solid lightgray;    
}

.widgets__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.widgets__header > .MuiSvgIcon-root {
    color: black;
    font-size: 16px;
}

.widgets__header > h2 {
    font-size: 16px;
    font-weight: 600;
}

.widgets__article {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widgets__article:hover {
    background-color: #f5f5f5;
}

.widgets__articleleft {
    color: blue;
    margin-right: 10px;
}

.widgets__articleleft > .MuiSvgIcon-root {
    font-size: 12px;
}

.widgets__articleright {
    flex: 1;
}

.widgets__articleright > h4 {
    font-size: 14px;
    font-weight: 600;
}

.widgets__articleright > p {
    font-size: 12px;
    color: gray;
}