.post {
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 60%);
    margin-top: 10px;
}

.post__header {
    display: flex;
    margin-bottom: 10px;
    border-bottom: 1px solid lightgray;
    padding-bottom: 10px;
}

.post__info {
    margin-left: 10px;
}

.post__info > p {
    font-size: 14px;
    color: gray;
}

.post__body {
    margin-bottom: 20px;
    overflow-wrap: anywhere;
    text-align: left;
    margin-top: 10px;

}

.post__info > h2 {
    font-size: 16px;
    font-weight: 600;
    text-align: left;
}

.post__buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    padding: 10px;
}