.header {
display: flex;
justify-content: space-evenly;
position: sticky;
border-bottom: .5px solid lightgray;
padding-top: 7px;
padding-bottom: 7px;
width: 100%;
top: 0;
background-color: white;
}

.header__left {
flex: 0.3;
display: flex;
align-items: center;
margin-left: 20px;
}

.header__search {
padding: 10px;
display: flex;
align-items: center;
border-radius: 6px;
height: 25px;
color: gray;
background-color: #eef3f8;
}

.header__search > input {
outline: none;
border: none;
background: none;

}

.header__left > .MuiSvgIcon-root {
margin-right: 5px;
}

.header__right {
display: flex;

}